.Main-page {
  -webkit-flex: 5 1;
          flex: 5 1;
  margin-bottom: 5rem;
}

.Front-page-image {
  max-height: 40rem;
  min-height: 40rem;
  width: 100%;
  overflow: hidden;
  z-index: -1;
  background-image: url(/static/media/chamonix.1def85b4.jpg);
  background-size: cover;
  background-position-y: 42%;
}

.Overlap-section {
  margin: -5rem auto 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 94%;
  height: 30rem;
  -webkit-align-self: center;
          align-self: center;
  z-index: 1;
}

.Overlap-section a {
  width: 33%;
  margin: 1rem;
  z-index: 0;
  background-color: white;
  text-decoration: none;
}

.Overlap-section a:hover .Overlap-image {
  opacity: 0.6;
  color: #efefef;
}

.Overlap-image {
  width: 100%;
  opacity: 0.9;
  background-color: white;
  height: 100%;
  font-weight: bolder;
  font-size: 3rem;
  background-size: cover;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
  -webkit-align-items: center;
          align-items: center;
  text-transform: uppercase;
  background-position: center;
}

.Overlap-label {
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.About-me {
  background-image: url(/static/media/pies.eacbcba5.jpg);
  background-size: cover;
  background-position: center;
  opacity: 0.9;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex: 5 1;
          flex: 5 1;
  position: relative;
  background-position: center;
}

.About-me-description {
